section.app-cpnt.schedule {
}
section.app-cpnt.footer {
  padding: 10%;
}

section.app-cpnt.footer > div {
  width: 100%;
  text-align: center;
  font-size: 40px;
  margin: 100px 0;
}

section.app-cpnt.footer > .frame > img {
  width: 100%;
}
section.app-cpnt.sub {
  position: relative;
  width: 40%;
  height: 100%;
  margin-left: auto;

  color: rgba(255,255,255,.9);
  background: #4c4f52;
}

section.app-cpnt.home {
  min-height: 600px;
  font-size: 20px;
}

body,div {
  font-family: 'hsst';
}

font.title-special {
  font-family: 'Sacramento', cursive;
}

section.app-cpnt > .title {
  text-align: center;
  font-size: 40px;
  margin: 30px 0 40px;
}

.app-cpnt .divie {
  width: 100%;
  border-bottom: 2px solid #888;
  margin-bottom: 30px;
}

@font-face {
  font-family: 'hsst';
  src: url(/static/media/hsst.cdf3e0f9.ttf);
}


@media screen and (max-width: 800px) {
  section.app-cpnt.main {
    width: 100%;
    position: static;
  }
  
  section.app-cpnt.sub {
    width: 100%;
  }
}

