section.app-cpnt.timeline .event {
  width: 100%;
  margin: 0 auto 100px;
  white-space: nowrap;
  position: relative;
}


section.app-cpnt.timeline .event > section {
  width: 250px;
  margin: 50px auto;
}

section.app-cpnt.timeline .event > section.time {
  position: absolute;
  top: 0;
  left: -webkit-calc(50% - 190px);
  margin: 0;
  width: 55px;
  font-size: 25px;
  text-align: right;
}


section.app-cpnt.timeline .event > section.map > div {
  width: 250px;
  margin-left: -webkit-calc(50% - 125px);
}

section.app-cpnt.timeline .divide {
  margin: 30px;
  position: relative;
}

section.app-cpnt.timeline .divide > span {
  display: inline-block;
}

section.app-cpnt.timeline .divide > span:first-child {
  width: 185px;
  white-space: nowrap;
}

section.app-cpnt.timeline .divide > span:last-child {
  width: -webkit-calc(100% - 210px);
  border: 1px solid #777;
  position: absolute;
  right: 10px;
  top: 7px;
}

section.app-cpnt.timeline section.gallery > img {
  width: 300px;
  height: 200px;
  object-fit: cover;
}

section.app-cpnt.timeline .title-location {
  margin: 80px auto 20px;
  width: 200px;
  text-align: center;
  font-size: 30px;
}

section.app-cpnt.timeline .event > section.map {
  overflow: hidden;
}

section.app-cpnt.timeline .event > .main {
  white-space: initial;
  font-size: 25px;
}



section.app-cpnt.timeline .event > .main > .title {
  padding-top: 3px;
  margin-bottom: 20px;
  font-size: 30px;
}

@media screen and (max-width: 1000px) {
  section.app-cpnt.timeline .event > section {
    width: 50%;
  }
  section.app-cpnt.timeline .event > section.time {
    left: -webkit-calc(25% - 65px);
  }

  section.app-cpnt.photo-frame>img {
    width: 250px;
    left: -25%;
  }
  
  section.app-cpnt.timeline .event>section.map {
    overflow: visible;
  }

  section.app-cpnt.timeline .event>.main>.address {
    width: 220px;
  }
}